getHTML.onclick = () => {
    const request = new XMLHttpRequest()
    request.open('GET', '/3.html')
    request.onload = () => {
        // åˆ›å»º div æ ‡ç­¾
        const div = document.createElement('div')
        // å¡«å†™ div å†…å®¹
        div.innerHTML = request.response
        // æ’å…¥åˆ°èº«ä½“é‡Œ
        document.body.appendChild(div)
    }
    request.onerror = () => {}
    request.send()
};

